* {
    margin: 0;
    padding: 0;
}
.slider {
    overflow: hidden;
    width: 100%;
    height: 100vh;
    position: relative;
}
.slider > ul{
    height: auto;
    overflow: hidden;
    position: relative;
    left: 0;
    -webkit-transition: left 1s;
    -o-transition: left 1s;
    transition: left 1s;
}
.slider .slider-item{
    display: inline-block;
    height: auto;
}
.slider .slider-item img{
    display: block;
    height: auto;
    width: 100%;
}
.slider-arrow {
    display: inline-block;
    color: #fff;
    font-size: 50px;
    position: absolute;
    top: 50%;
    margin-top: -50px;
    z-index: 100;
    padding: 20px;
    cursor: pointer;
    font-weight: bold;
}
.slider-arrow:hover{
    background: rgba(0,0,0,.2);
}
.slider-arrow-right {
    right: 0;
}
.slider-arrow-left {
    left: 0;
}
.slider-dots-wrap {
    z-index: 99;
    text-align: center;
    width: 100%;
    position: absolute;
    bottom: 0;
}
.slider-dot{
    display: inline-block;
    width: 6px;
    height: 6px;
    border: 3px solid #ccc;
    margin: 6px;
    cursor: pointer;
    border-radius: 20px;
}
.slider-dot:hover{
    border: 3px solid #868686;
}
.slider-dot-selected{
    background: #ccc;
}




